import axios from 'axios';
import notification from '@isomorphic/shared/isomorphic/components/Notification';

// localStorage.js
const FileDownloader = (url, name) => {
  axios
    .get(`/api/files?url=` + url, {
      responseType: 'blob',
      timeout: 30000,
      headers: { Authorization: localStorage.getItem('id_token') || undefined },
    })
    .then(response => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', name);
      document.body.appendChild(link);
      link.click();
    })
    .catch(e => {
      console.log(e);
      notification('error', 'Something wrong in downloading data');
    });
};
export default FileDownloader;
