import React, { useState, useEffect } from 'react';
import {
  Row,
  Col,
  Divider,
  Typography,
  Table,
  Button,
  Icon,
  Input,
  DatePicker,
  Popconfirm,
  Select,
  Upload,
} from 'antd';
import basicStyle from '../../settings/basicStyle';
import LayoutContentWrapper from '@isomorphic/shared/isomorphic/components/utility/layoutWrapper';
import moment from 'moment';
import SuperFetch from '../../helpers/superFetch';
import PaymentReprotBtn from './PaymentReprotBtn.js';
import { constant } from '../../settings';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import FileDownloader from '../../helpers/fileDownloader';
export default function Payment() {
  // Declare a new state variable, which we'll call "count"
  const [flag, setFlag] = useState(false);
  const [claimPending, setClaimPending] = useState([]);
  const [reportToAccountant, setReportToAccountant] = useState([]);
  const [claimPaid, setClaimPaid] = useState([]);
  const [, setPaymentReceipts] = useState([]);
  const [loading, setLoading] = useState(true);
  const { rowStyle } = basicStyle;
  useEffect(() => {
    const abortController = new AbortController();
    const fetchData = async () => {
      setLoading(true);
      let claimPendingRes = await SuperFetch.get('payment/toBePaid');
      let claimPaidRes = await SuperFetch.get('payment/paid');
      let paymentReceiptsRes = await SuperFetch.get('payment/paymentReceipt');
      let reportToAccountantRes = await SuperFetch.get(
        'payment/reportToAccountant'
      );

      setClaimPending(claimPendingRes.data);
      setClaimPaid(claimPaidRes.data);
      setPaymentReceipts(paymentReceiptsRes.data);
      setReportToAccountant(reportToAccountantRes.data);
      setLoading(false);
    };
    fetchData();
    return () => {
      abortController.abort();
    };
  }, [flag]);

  // const fetchPaymentReceiptsData = async () => {
  //   setLoading(true);
  //   let paymentReceiptsRes = await SuperFetch.get('payment/paymentReceipt');
  //   setPaymentReceipts(paymentReceiptsRes.data);
  //   setLoading(false);
  // };

  const fetchClaimPandingandReportToAccountant = async () => {
    setLoading(true);
    let claimPendingRes = await SuperFetch.get('payment/toBePaid');
    setClaimPending(claimPendingRes.data);
    let reportToAccountantRes = await SuperFetch.get(
      'payment/reportToAccountant'
    );
    setReportToAccountant(reportToAccountantRes.data);
    setLoading(false);
  };

  const onButtonClickClaim = () => {
    SuperFetch.get('payment/sendToAccountant').then(response => {
      console.log(response);
      fetchClaimPandingandReportToAccountant();
    });
  };
  const refHandler = (e, record) => {
    SuperFetch.put('claim/claim', {
      id: record.id,
      reimbursementRef: e.currentTarget.value,
    })
      .then(response => {
        console.log(response);
        if (response.status === 200) {
          notification('success', 'Reimbursement Ref inserted!');
        } else {
          notification('error', 'something goes wrong');
        }
      })
      .catch(e => console.log(e));
  };

  const DatePaidHandler = (e, value, record) => {
    SuperFetch.put('claim/claim', { id: record.id, PaidAt: value })
      .then(response => {
        console.log(response);
        if (response.status === 200) {
          notification('success', 'Date Paid inserted!');
        } else {
          notification('error', 'something goes wrong');
        }
      })
      .catch(e => console.log(e));
  };
  const handleSearch = (selectedKeys, confirm, record) => {
    confirm();
  };
  const handleChange = (value, select, record) => {
    console.log(select.props);
    reportToAccountant[
      reportToAccountant.findIndex(i => i.id === record.id)
    ].option = select.props;
    console.log(reportToAccountant);
  };
  const columns = [
    {
      title: 'UCN',
      dataIndex: 'id',
      render: (text, record) => text,
    },
    {
      title: 'Status',
      dataIndex: 'status',
      render: (text, record) => text,
    },
    {
      title: 'Email Address',
      dataIndex: 'contactEmail',
      render: (text, record) => text,
    },
    {
      title: 'Phone Number',
      dataIndex: 'contactPhoneNumber',
      render: (text, record) =>
        (record.contactPhoneNumberCountryCode
          ? record.contactPhoneNumberCountryCode + '-'
          : '') + text,
    },
    {
      title: "Client's Address",
      dataIndex: 'contactHomeAddress',
      render: (text, record) => text,
    },
    {
      title: 'Bank Account No',
      dataIndex: 'bankAccountNumber',
      render: (text, record) => text,
    },
    {
      title: 'Account Holder Name',
      dataIndex: 'accountHoldersName',
      render: (text, record) => text,
    },
    {
      title: 'Bank Name',
      dataIndex: 'bankName',
      render: (text, record) => text,
    },
    {
      title: 'Bank Address',
      dataIndex: 'bankAddress',
      render: (text, record) => text,
    },
    {
      title: 'SWIFT Code',
      dataIndex: 'swift',
      render: (text, record) => text,
    },
    {
      title: 'IBAN Code',
      dataIndex: 'ibanCodeSortCode',
      render: (text, record) => text,
    },
    {
      title: 'Date Apprved',
      dataIndex: 'approvedAt',
      render: (text, record) => {
        return moment(text).format('DD MMM YYYY');
      },
    },
  ];
  const columnsReportToAccountant = [
    {
      title: 'Created Date',
      dataIndex: 'createdAt',
      render: (text, record) => moment(text).format('DD MMM YYYY'),
    },
    {
      title: 'Name',
      dataIndex: 'name',
      render: (text, record) => {
        return (
          <Button
            type="link"
            style={{ padding: 0 }}
            onClick={e => {
              console.log(record.name + '.xlsx');
              FileDownloader(record.url, record.name + '.xlsx');
            }}
          >
            <span style={{ color: '#1890ff' }}>{text}</span>
          </Button>
        );
      },
    },
    {
      title: 'Payment Slipt',
      dataIndex: 'PaymentReceiptsToAccounts',
      render: (text, record) => {
        return (
          <Select
            style={{ width: 120 }}
            onChange={(value, select) => handleChange(value, select, record)}
          >
            {record.PaymentReceiptsToAccounts.map(option => {
              //console.log(option)
              return (
                <Select.Option key={option.id} value={option.url}>
                  {option.name}
                </Select.Option>
              );
            })}
          </Select>
        );
      },
    },
    {
      title: 'Action',
      key: 'action',
      render: (text, file, i) => {
        return (
          <Row>
            <Col span={2}>
              <Upload
                name={'file'}
                action={'/api/payment/paymentReceipt2'}
                showUploadList={false}
                headers={{
                  Authorization: localStorage.getItem('id_token') || undefined,
                }}
                data={{ PaymentToAccountantsId: file.id }}
                onChange={info => {
                  switch (info.file.status) {
                    case 'done':
                      setFlag(!flag);
                      //console.log(info.file.response)
                      break;
                    case 0:
                      break;
                    default:
                      break;
                  }
                }}
              >
                <Button icon="upload"></Button>
              </Upload>
            </Col>
            <Col span={2}>
              <Button
                icon="download"
                // icon="delete"
                onClick={() => {
                  console.log(file.option);
                  if (!file.option) {
                    notification('error', 'Please select a slipt');
                    return;
                  }
                  FileDownloader(file.option.value, file.option.children);
                }}
              />
            </Col>
            <Col span={8}>
              <Button
                icon="mail"
                // icon="delete"
                onClick={() => {
                  //resend email
                  SuperFetch.get('payment/resendToAccountant', file).then(
                    response => {
                      if (response.status === 200) {
                        notification('success', 'Email sending successeed!');
                        response.data.accepted.forEach(email => {
                          notification('success', 'Success ' + email);
                        });
                        response.data.rejected.forEach(email => {
                          notification('error', 'Fail ' + email);
                        });
                      } else {
                        notification(
                          'error',
                          'something goes wrong with email'
                        );
                      }
                    }
                  );
                }}
              />
            </Col>
          </Row>
        );
      },
    },
  ];
  const columnsPaid = [
    {
      title: 'Date Approved',
      dataIndex: 'approvedAt',
      filterDropdown: ({
        setSelectedKeys,
        selectedKeys,
        confirm,
        clearFilters,
      }) => (
        <div style={{ padding: 8 }}>
          <Row type="flex" justify="start" style={{ marginBottom: 8 }}>
            <DatePicker.RangePicker
              value={selectedKeys[0]}
              onChange={e => {
                setSelectedKeys(e ? [e] : []);
              }}
              onPressEnter={() => handleSearch(selectedKeys, confirm)}
            />
          </Row>
          <Row type="flex" justify="end">
            <Button
              onClick={() => handleSearch(selectedKeys, confirm)}
              type="primary"
              size="small"
            >
              Search
            </Button>
            <Button type="primary" onClick={() => clearFilters()} size="small">
              Reset
            </Button>
          </Row>
        </div>
      ),
      //
      onFilter: (value, record) => {
        return (
          moment(record.updatedAt).isAfter(value[0]) &&
          moment(record.updatedAt).isBefore(value[1])
        );
      },
      render: (text, record) => {
        return moment(text).format('DD MMM YYYY');
      },
    },
    {
      title: 'Spreadsheet created Date',
      dataIndex: 'sendToAccountantAt',
      filterDropdown: ({
        setSelectedKeys,
        selectedKeys,
        confirm,
        clearFilters,
      }) => (
        <div style={{ padding: 8 }}>
          <Row type="flex" justify="start" style={{ marginBottom: 8 }}>
            <DatePicker.RangePicker
              value={selectedKeys[0]}
              onChange={e => {
                setSelectedKeys(e ? [e] : []);
              }}
              onPressEnter={() => handleSearch(selectedKeys, confirm)}
            />
          </Row>
          <Row type="flex" justify="end">
            <Button
              onClick={() => handleSearch(selectedKeys, confirm)}
              type="primary"
              size="small"
            >
              Search
            </Button>
            <Button type="primary" onClick={() => clearFilters()} size="small">
              Reset
            </Button>
          </Row>
        </div>
      ),
      //
      onFilter: (value, record) => {
        return (
          moment(record.sendToAccountantAt).isAfter(value[0]) &&
          moment(record.sendToAccountantAt).isBefore(value[1])
        );
      },
      render: (text, record) => {
        return moment(text).format('DD MMM YYYY');
      },
    },
    {
      title: 'UCN',
      dataIndex: 'id',
      filterDropdown: ({
        setSelectedKeys,
        selectedKeys,
        confirm,
        clearFilters,
      }) => (
        <div style={{ padding: 8 }}>
          <Row type="flex" justify="start" style={{ marginBottom: 8 }}>
            <Input
              value={selectedKeys[0]}
              onChange={e =>
                setSelectedKeys(e.target.value ? [e.target.value] : [])
              }
              onPressEnter={() => handleSearch(selectedKeys, confirm)}
            />
          </Row>
          <Row type="flex" justify="end">
            <Button
              type="primary"
              onClick={() => handleSearch(selectedKeys, confirm)}
              size="small"
            >
              Search
            </Button>
            <Button type="primary" onClick={() => clearFilters()} size="small">
              Reset
            </Button>
          </Row>
        </div>
      ),
      //
      onFilter: (value, record) => {
        console.log(record.id.toLowerCase().includes(value.toLowerCase()));
        return record.id.toLowerCase().includes(value.toLowerCase());
      },
      render: (text, record) => text,
    },
    {
      title: 'Policy Holder Name',
      dataIndex: 'contactEmail',
      filterDropdown: ({
        setSelectedKeys,
        selectedKeys,
        confirm,
        clearFilters,
      }) => (
        <div style={{ padding: 8 }}>
          <Row type="flex" justify="start" style={{ marginBottom: 8 }}>
            <Input
              value={selectedKeys[0]}
              onChange={e =>
                setSelectedKeys(e.target.value ? [e.target.value] : [])
              }
              onPressEnter={() => handleSearch(selectedKeys, confirm)}
            />
          </Row>
          <Row type="flex" justify="end">
            <Button
              type="primary"
              onClick={() => handleSearch(selectedKeys, confirm)}
              size="small"
            >
              Search
            </Button>
            <Button type="primary" onClick={() => clearFilters()} size="small">
              Reset
            </Button>
          </Row>
        </div>
      ),
      //
      onFilter: (value, record) => {
        console.log(record.id.toLowerCase().includes(value.toLowerCase()));
        return (
          record.policyFirstName +
          ' ' +
          record.policyLastName.toLowerCase().includes(value.toLowerCase())
        );
      },
      render: (text, record) =>
        record.policyFirstName + ' ' + record.policyLastName,
    },
    {
      title: 'Reimbursement Amount',
      dataIndex: 'contactPhoneNumber',
      render: (text, record) => {
        return (
          (
            record.ClaimInfoVisits.reduce((visitAcc, visitCurr) => {
              return (
                visitAcc +
                visitCurr.BillingInfos.reduce((billingAcc, billingCurr) => {
                  return billingAcc + billingCurr.value;
                }, 0)
              );
            }, 0) * record.RCExchangeRate
          ).toFixed(constant.fixedDigital) +
          ' ' +
          record.reimbusementCurrency
        );
      },
    },
    {
      title: 'Reimbursement Ref',
      dataIndex: 'contactHomeAddress',
      render: (text, record) => {
        return (
          <Input
            onPressEnter={e => refHandler(e, record)}
            onBlur={e => refHandler(e, record)}
            defaultValue={record.reimbursementRef}
          />
        );
      },
    },
    {
      title: 'Date Paid',
      dataIndex: 'bankAccountNumber',
      render: (text, record) => {
        return (
          <DatePicker
            defaultValue={record.PaidAt ? moment(record.PaidAt) : null}
            onChange={(e, value) => DatePaidHandler(e, value, record)}
            format={'DD MMM YYYY'}
          />
        );
      },
    },
  ];
  // const columnsReceipts = [
  //   {
  //     title: 'Payment Slipt Received Date',
  //     dataIndex: 'receivedAt',
  //     render: (text, record) => {
  //       return moment(text).format('DD MMM YYYY');
  //     },
  //   },
  //   {
  //     title: 'File Name',
  //     dataIndex: 'name',
  //     render: (text, record) => text,
  //   },
  //   {
  //     title: 'URL',
  //     dataIndex: 'url',
  //     render: (text, record) => text,
  //   },
  //   {
  //     title: 'Action',
  //     key: 'action',
  //     render: (text, file) => {
  //       return (
  //         <div className="isoInvoiceBtnView">
  //           <Button
  //             icon="download"
  //             // icon="delete"
  //             onClick={() => {
  //               console.log(file);
  //               FileDownloader(file.url, file.name);
  //             }}
  //           ></Button>
  //         </div>
  //       );
  //     },
  //   },
  // ];

  return (
    <LayoutContentWrapper>
      <div>
        <Row>
          <Col span={20} offset={2}>
            <Row style={rowStyle}>
              <Col>
                <Typography.Title>Payment</Typography.Title>
              </Col>
              <Divider />
            </Row>
            <Row
              style={{
                border: 'solid',
                borderRadius: '30px',
                borderColor: 'white',
                padding: 25,
                borderWidth: '1px',
                ...rowStyle,
              }}
              type="flex"
              justify="space-between"
            >
              <Col>
                <Typography.Title level={4}>
                  Claims to be paid:
                </Typography.Title>
              </Col>
              <Col>
                <Popconfirm
                  title="Are you sure to move all claims to be paid to claims paid?"
                  onConfirm={onButtonClickClaim}
                  okText="Yes"
                  cancelText="No"
                >
                  <Button type="link">
                    <Icon component={PaymentReprotBtn} />
                  </Button>
                </Popconfirm>
              </Col>
              <Table
                columns={columns}
                //rowSelection={rowSelection}
                dataSource={claimPending ? claimPending : []}
                style={{ color: 'white', width: '100%' }}
                rowKey="id"
                size={'small'}
                indentSize={1}
                pagination={{ pageSize: 5 }}
                loading={loading}
              />
            </Row>
            <Row
              style={{
                border: 'solid',
                borderRadius: '30px',
                borderColor: 'white',
                padding: 25,
                borderWidth: '1px',
                ...rowStyle,
              }}
              type="flex"
              justify="space-between"
            >
              <Col>
                <Typography.Title level={4}>
                  Report To Accountant:
                </Typography.Title>
              </Col>
              <Table
                columns={columnsReportToAccountant}
                dataSource={reportToAccountant ? reportToAccountant : []}
                style={{ color: 'white', width: '100%' }}
                rowKey="id"
                size={'small'}
                indentSize={1}
                pagination={{ pageSize: 5 }}
                loading={loading}
              />
            </Row>
            <Row style={rowStyle}>
              <div
                style={{
                  border: 'solid',
                  borderRadius: '30px',
                  borderColor: 'white',
                  padding: 25,
                  borderWidth: '1px',
                  width: '100%',
                }}
              >
                <Col>
                  <Typography.Title level={4}>Claim paid:</Typography.Title>
                </Col>
                <Col></Col>

                <Table
                  columns={columnsPaid}
                  dataSource={claimPaid ? claimPaid : []}
                  style={{ color: 'white', width: '100%' }}
                  rowKey="id"
                  size={'small'}
                  indentSize={1}
                  pagination={{ pageSize: 5 }}
                  loading={loading}
                />
              </div>
            </Row>
            {/* <Row
              style={{
                border: 'solid',
                borderRadius: '30px',
                borderColor: 'white',
                padding: 25,
                borderWidth: '1px',
                ...rowStyle,
              }}
              type="flex"
              justify="space-between"
            >
              <Col>
                <Typography.Title level={4}>Payment Splits:</Typography.Title>
              </Col>
              <Col>
                <Button type="primary" onClick={() => setIsUploadVisible(true)}>
                  Upload Payment Document
                </Button>
                <UploadReceiptsForm
                  isUploadVisible={isUploadVisible}
                  onCancel={() => setIsUploadVisible(false)}
                  fetchPaymentReceiptsData={fetchPaymentReceiptsData}
                />
              </Col>
              <Table
                columns={columnsReceipts}
                dataSource={paymentReceipts ? paymentReceipts : []}
                style={{ color: 'white', width: '100%' }}
                rowKey="id"
                size={'small'}
                indentSize={1}
                pagination={{ pageSize: 5 }}
                loading={loading}
              />
            </Row> */}
          </Col>
        </Row>
      </div>
    </LayoutContentWrapper>
  );
}
